<template>
  <div>
    <h1>Создать дело</h1>
    <main-button @click="showWindow">Создать дело</main-button>
    <modal-window v-model:show="windowVisible">
      <form @submit.prevent class="todo__form">
        <input v-model.trim="title" type="text" placeholder="Событие" />
        <textarea v-model="description" placeholder="Описание"></textarea>
        <input v-model="day" type="date" />
        <input v-model="time" type="time" />
        <main-button @click="createTask">Отправить</main-button>
      </form>
    </modal-window>
    <h1>Список дел</h1>
    <div class="todo__body" v-for="task in tasks" :key="task">
      <div class="todo__title">{{ task.title }}</div>
      <div class="todo__description">{{ task.description }}</div>
      <div class="todo__day">{{ task.day }}</div>
      <div class="todo__time">{{ task.time }}</div>
      <main-button @click="deletTask">Удалить</main-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: 0,
      title: "",
      description: "",
      day: "",
      time: "",
      windowVisible: false,
      tasks: [
        {
          id: 1,
          title: "Покушать",
          description: "Надо съесть тортик",
          day: "2019-08-22",
          time: "20:00",
        },
        {
          id: 2,
          title: "Учеба",
          description: "Заняться программированием",
          day: "2019-11-03",
          time: "09:00",
        },
        {
          id: 3,
          title: "Работа",
          description: "Устроиться на работу",
          day: "2019-03-15",
          time: "12:00",
        },
      ],
    };
  },
  methods: {
    createTask() {
      const newTask = {
        id: Date.now(),
        title: this.title,
        description: this.description,
        day: this.day,
        time: this.time,
      };
      this.tasks.push(newTask);
      this.title = "";
      this.description = "";
      this.day = "";
      this.time = "";
      this.windowVisible = false;
    },
    showWindow() {
      this.windowVisible = true;
    },
  },
};
</script>

<style lang="scss" scoped>
$main_font: "Courier New", Courier, monospace;
$bg_color: lightgray;

h1 {
  font-family: $main_font;
  margin: 2.5% 0;
  text-align: center;
  &:nth-child(1) {
    text-align: start;
  }
}
.todo__form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  input {
    font-family: $main_font;
    margin: 1.5% 0;
    padding: 0 2.5%;
    width: 80%;
    height: 50px;
    border-radius: 25px;
    border: none;
    background: $bg_color;
    font-size: 25px;
  }
  textarea {
    font-family: $main_font;
    padding: 1% 2.5%;
    width: 80%;
    height: 100px;
    border-radius: 25px;
    border: none;
    background: $bg_color;
    font-size: 25px;
    resize: none;
  }
  button {
    font-family: $main_font;
    font-size: 18px;
  }
}
.todo__body {
  margin: 2% 0;
  width: 40%;
  padding: 2% 3%;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 25px;
  border-radius: 25px;
  background: $bg_color;

  button {
    margin-left: 70%;
  }
}
</style>