<template>
  <div class="dialog" v-if="show === true" @click="hideWindow">
      <div class="dialog__body" @click.stop>
          <slot></slot>
      </div>
  </div>
</template>

<script>
export default {
    name: 'modal-window',
    props: {
        show: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        hideWindow(){
            this.$emit('update:show', false)
        }
    }
}
</script>

<style lang="scss" scoped>
.dialog{
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    position: fixed;
    z-index: 1;
    display: flex;
}
.dialog__body{
    padding: 3% 2%;
    margin: auto;
    background: white;
    border-radius: 25px;
    width: 40%;
}
</style>